.container {
  display: flex;
  height: calc(100vh - 60px);
  flex-direction: column;
  padding: 12px;
  padding-bottom: 0; }

.division {
  padding: 12px;
  width: 320px;
  background-color: #32393d;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  margin-bottom: 12px; }

.scrollable {
  height: 70px;
  overflow-y: auto;
  flex-grow: 100; }
  .scrollable::-webkit-scrollbar-track {
    border-radius: 100px;
    background-color: #32393d; }
  .scrollable::-webkit-scrollbar {
    width: 7px;
    background-color: #32393d;
    border-radius: 100px; }
  .scrollable::-webkit-scrollbar-thumb {
    background-color: #1e2024;
    border-radius: 100px; }

.growdiv {
  flex-grow: 100; }

.sidebar {
  width: 100%; }
  .sidebarEl {
    font-size: 16px;
    color: white;
    display: flex;
    width: 100%;
    align-items: center; }
    .sidebarEl:hover {
      background-color: #1e2024;
      cursor: pointer;
      border-radius: 6px; }
  .sidebar_icon {
    font-size: 12px;
    margin: 10px 12px;
    color: white;
    background-color: red; }

.user {
  position: relative;
  display: flex;
  align-items: center;
  margin: 0 0 15px 0; }
  .user > img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin-right: 15px;
    object-fit: cover;
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none; }
  .user > .about {
    height: min-content; }
  .user .online {
    position: absolute;
    top: 45px;
    left: 45px;
    height: 25px;
    width: 25px;
    border-radius: 100px;
    background-color: #66bb6a;
    border: solid #32393d 7px; }
  .user span {
    display: block;
    line-height: 1.3; }
  .user .username {
    color: white;
    font-size: 21px; }
  .user .activity {
    color: #bdc3c7;
    font-size: 13px; }

.friend {
  text-decoration: none;
  position: relative;
  margin-right: 12px;
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 5px; }
  .friend:hover {
    background-color: #1e2024; }
    .friend:hover .fonline {
      border: solid #1e2024 5px; }
  .friend img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none; }
  .friend .online {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    position: absolute;
    top: 40px;
    left: 40px;
    border: solid #32393d 5px; }
  .friend .username {
    display: block;
    color: white;
    font-size: 14, 5px;
    line-height: 1.2; }
  .friend .activity {
    display: block;
    color: #bdc3c7;
    font-size: 13px;
    line-height: 1.2; }
  .friend .about {
    margin-left: 10px; }

.yellow {
  background-color: #cf8300; }

.red {
  background-color: #c03b3b; }

.green {
  background-color: #66bb6a; }

.black {
  background-color: #585e6a; }

.gamediv {
  margin-right: 12px;
  align-items: center;
  display: flex;
  padding: 10px; }
  .gamediv:hover {
    background-color: #1e2024;
    border-radius: 5px; }

span:focus {
  border-bottom: 3px solid #ff9a60 !important;
  color: white !important; }

.gameicon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
  object-fit: cover;
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none; }

.gamename {
  color: white;
  font-size: 15px;
  flex-grow: 100; }

.playicon {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ff9a60;
  margin: 0 10px;
  cursor: pointer; }
  .playicon:hover {
    background-color: #ffaa7a;
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.5); }
  .playicon i {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    font-size: 10px;
    color: white; }

.tab-control {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px; }
  .tab-control span {
    text-align: center;
    padding: 13px 0;
    font-weight: 500;
    font-size: 14px;
    width: 100%;
    border-radius: 8px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-bottom: 3px solid #41454e;
    cursor: pointer;
    transition: 0.2s all;
    color: #bfbfbf; }
    .tab-control span:hover {
      color: white; }
